#home {
  display: none;
}

.menu-icon {
  display: none;
}

header {
  width: 100%;
  display: flex;
  justify-content: center;
}

header nav {
  width: 90%;
  background-color: #ebebeb;
  text-align: center;
  position: fixed;
  z-index: 3;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

header nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}

header nav ul li {
  font-family: "Roboto Condensed", sans-serif;
  padding: 20px;
  text-decoration: none;
  display: block;
  color: #333;
}

header nav ul li:hover {
  background-color: #e7e5e5;
}

header nav ul img {
  width: 4rem;
}

@media screen and (max-width: 894px) {

  header {
    display: block;
    position: fixed;
    top: 0;
    z-index: 3;
  }

  #logo {
    display: none;
  }

  #home {
    display: block;
  }

  header nav {
    width: 100%;
    height: auto;
    margin: 0;
    margin-top: -.1rem;
    display: block;
  }

  header nav ul {
    display: none;
    /* margin-top:-.1rem; */
  }

  header nav ul li {
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }

  .menu-bar {
    display: block;
    background: #ebebeb;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    margin-bottom: 0;
  }

  .menu-bar .menu-icon {
    display: block;
    padding: 1rem;
    background: #ebebeb;
    width: 2rem;
  }
}

.skills-container{
    width: 100%;
    margin-top: 1rem;
}

.skills-container section{
    width: 90%;
    margin: auto auto;
    z-index: 2;
}

